import React, { Fragment } from "react";
import { Checkbox, Button, Tooltip } from "antd";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTruckMoving, faExchangeAlt } from '@fortawesome/free-solid-svg-icons'

const colunasRoterizador: any = [
    {
        title: 'Regio',
        dataIndex: 'regiao',
        key: 'regiao',
        sorter: (a: any, b: any) => a.regiao.length - b.regiao.length,
    },
    {
        title: 'Unidade',
        dataIndex: 'unidade',
        key: 'unidade',
        sorter: (a: any, b: any) => a.unidade.length - b.unidade.length
    },
    {
        title: 'Geo',
        dataIndex: 'geo',
        key: 'geo',
        sorter: (a: any, b: any) => a.geo.length - b.geo.length
    },
    { title: 'Capacidade Disp (HL)', dataIndex: 'capacidadeDispHl', key: 'capacidadeDispHl' },
    { title: 'Volume de falta', dataIndex: 'volumeFalta', key: 'volumeFalta' },
    { title: 'Volume (HL)', dataIndex: 'volumeHl', key: 'volumeHl' },
    {
        title: 'Nr. Exportaes',
        dataIndex: 'exportacoes',
        key: 'exportacoes',
        sorter: (a: any, b: any) => a.exportacoes - b.exportacoes
    },
    {
        title: 'Hr ltimo exp.',
        dataIndex: 'horarioUltimoExp',
        key: 'horarioUltimoExp',
    },
    , {
        title: 'Unidades',
        dataIndex: 'unidadesTransferencia',
        key: 'unidadesTransferencia',
        render: () => (<Checkbox></Checkbox>)
    },
    {
        title: 'Aes',
        dataIndex: 'acoes',
        key: 'acoes',
        render: () => (
            <div style={{ display: 'flex' }}>
                <Tooltip title="Roteirizar">
                    <div style={{
                        width: '32px', height: '32px', display: 'flex', justifyContent: 'center', marginRight: '10px',
                        alignItems: 'center', backgroundColor: '#F64429', borderRadius: '8px', cursor: 'pointer'
                    }}>
                        <FontAwesomeIcon color="#FFF" icon={faTruckMoving} />
                    </div>
                </Tooltip>
                <Tooltip title="Transferir">
                    <div style={{
                        width: '32px', height: '32px', display: 'flex', justifyContent: 'center',
                        alignItems: 'center', backgroundColor: '#FF6A12', borderRadius: '8px', cursor: 'pointer'
                    }}>
                        <FontAwesomeIcon color="#FFF" icon={faExchangeAlt} />
                    </div>
                </Tooltip>
            </div>
        )
    }
];

export { colunasRoterizador };