import React, { FunctionComponent } from "react";
import { HeaderContent } from "./header.styled";

type HeaderProps = {
    title: string;
}

export const Header: FunctionComponent<HeaderProps> = ({ title }) => {
    return (
        <HeaderContent>
            <h2 className="gradient-text">
                {title}
            </h2>
        </HeaderContent>
    );
}