import styled from "styled-components";

export const PainelRoterizador = styled.div`
    @media screen and (max-width: 1430px) {
        .select-size {
            width: 10em !important;
        }
    }

    .align-right {
        display: flex;
        justify-content: flex-end;
        padding-top: 40px;
    }
    .select-size {
        width: 15em;
    }
    .title-content {
        margin-bottom: 24px;
        margin-top: 24px;
        >.title{
            font-weight: bold;
            font-size: 20px;
            color: #000000;
            margin-left: 45px;
        }
    }
    .filter-content {
        background: #FFFFFF;
        margin-left: 45px;
        width: 95%;
        padding: 32px;
        border-radius: 8px;
        display: flex;
        justify-content: space-between;
        .button-align {
            align-self: center;
        }
    }
`;

export const TableAlign = styled.div`
    width: 95%;
    margin-left: 45px;
    padding-top: 50px;
`;