import { getConfig } from '../config';
import { PainelRoterizadorModel } from '../metadata/painel-roterizador.model';

const getPainelRoterizador = async (geo: string): Promise<PainelRoterizadorModel[]> => {
  const config = getConfig();
  const response = await fetch(`${config.apiBasePath}/painel-roterizador`, {
    method: 'GET',
  });

  if (!response.ok) {
    throw new Error('No foi possivel buscar os dados do roterizador');
  }

  const payload = (await response.json()) as PainelRoterizadorModel[];

  return payload.filter(x => x.geo === geo);
};

export { getPainelRoterizador };
