import React, { useState, useRef, useEffect } from 'react';
import { getPainelRoterizador } from '../../services/painel-roterizador.service';
import { Button, Select } from 'antd';
import { Header } from '../header/header';
import { PainelRoterizador } from './painel-roteirizador.styled';
import { UiSelect } from '../ui/select/ui-select';
import { SelectContainer } from '../ui/select/ui-select.styled';
import { UiTitle } from '../ui/ui-title/iu-title';
import { TabelaRoteirizador } from './tabela-roteirizador/tabela-roteirizador';
import { PainelRoterizadorModel } from '../../metadata/painel-roterizador.model';
import { TabelaSimulacao } from './tabela-simulacao/tabela-simulacao';

const PainelPriorizador = () => {
  const [painelRoterizador, setPainelRoterizador] = useState<PainelRoterizadorModel[]>([]);
  const listaGeo = ['Sul', 'SP', 'Rio', 'Norte', 'Minas', 'CO', 'PR/SPI', 'BAHIA'];
  const listaUnidade = ['Blumenau', 'BC', 'Mooca', 'Diadema'];
  const listaRegioes = ['SP', 'Vale+Litoral'];
  const [mostrarSimulacao, setMostrarSimulacao] = useState(false);

  const BuscarDados = async (geo: string) => {
    const painelRoterizadorResponse = await getPainelRoterizador(geo);

    setPainelRoterizador(painelRoterizadorResponse);
  }

  const Simular = () => {
    setMostrarSimulacao(true);
    window.scrollTo({ top: window.innerHeight, behavior: 'smooth' });
  }

  return (
    <PainelRoterizador>
      <Header title="Hercules"></Header>
      <UiTitle text="Painel Roterizador"></UiTitle>
      <div className="filter-content">
        <SelectContainer>
          <UiSelect label="Geo">
            <Select className="select-size" size="large" defaultValue="Selecione a Geo" onChange={(value) => BuscarDados(value)}>
              {listaGeo.map(geo => <Option value={geo}>{geo}</Option>)}
            </Select>
          </UiSelect>
          <UiSelect label="Unidade">
            <Select className="select-size" size="large" defaultValue="Selecione a unidade">
              {listaUnidade.map(unidade => <Option value={unidade}>{unidade}</Option>)}
            </Select>
          </UiSelect>
          <UiSelect label="Regio">
            <Select className="select-size" size="large" defaultValue="Selecione a regio">
              {listaRegioes.map(regiao => <Option value={regiao}>{regiao}</Option>)}
            </Select>
          </UiSelect>
        </SelectContainer>
        <div className="button-align">
          <Button type="primary">Buscar</Button>
        </div>
      </div>
      <TabelaRoteirizador simulacaoButton={() => { Simular(); }} dados={painelRoterizador}></TabelaRoteirizador>
      {mostrarSimulacao ? <TabelaSimulacao dados={painelRoterizador}></TabelaSimulacao> : ''};
    </PainelRoterizador>
  );
};

export default PainelPriorizador;
