import React, { FunctionComponent } from "react";
import { TableAlign } from "../painel-roteirizador.styled";
import { Table, Button, Layout } from "antd";
import { colunasRoterizador } from "./colunas/colunas-roterizador";
import { PainelRoterizadorModel } from "../../../metadata/painel-roterizador.model";
const { Content } = Layout;

type PainelRoterizador = {
    dados: PainelRoterizadorModel[],
    simulacaoButton: Function
}

export const TabelaRoteirizador: FunctionComponent<PainelRoterizador> = ({ dados, simulacaoButton }) => {

    const HandleSimulacao = () => {
        simulacaoButton();
    }

    return (
        <TableAlign>
            <Content>
                <Table dataSource={dados}
                    columns={colunasRoterizador} pagination={false}
                    scroll={{ y: 400 }}
                />
            </Content>
            <div className="align-right">
                <Button type="primary" onClick={() => HandleSimulacao()}>
                    Simular Transferncia
                </Button>
            </div>
        </TableAlign>);
}