import React, { FunctionComponent } from "react";
import { SelectContent, SelectContainer } from "./ui-select.styled";

type UiSelectProps = {
    label: string,
}

export const UiSelect: FunctionComponent<UiSelectProps> = ({ label, children }) => {
    return (
        <SelectContent>
            <label>{label}</label>
            {children}
        </SelectContent>);
}

export const UiSelectContainer: FunctionComponent = () => <SelectContainer></SelectContainer>;