import { TableAlign } from "../painel-roteirizador.styled";
import React, { FunctionComponent } from "react";
import { Table, Layout } from "antd";
import { PainelRoterizadorModel } from "../../../metadata/painel-roterizador.model";
import { colunasSimulacao } from "./colunas/colunas-simulacao";
const { Content } = Layout;

type PainelRoterizador = {
    dados: PainelRoterizadorModel[],
}

export const TabelaSimulacao: FunctionComponent<PainelRoterizador> = ({ dados }) => {
    return (
        <TableAlign>
            <Content>
                <Table dataSource={dados}
                    columns={colunasSimulacao} pagination={false}
                    scroll={{ y: 400 }}
                />
            </Content>
        </TableAlign>);
}